import { useState, useEffect } from 'react';
import { Container, Row, Col } from 'react-bootstrap';
import {
  HTMLTable,
  InputGroup,
  Button,
  RangeSlider,
  NumericInput,
  HTMLSelect
} from '@blueprintjs/core';
import { Popover2 } from '@blueprintjs/popover2';

import data from '../Items.json';
import { price } from '../helper';

const buy_range_max = 150000;
const sell_range_max = 10000;

function App() {
  const [list_items, set_list_items] = useState([]);
  const [filter_name, set_filter_name] = useState('');
  const [filter_buy_range, set_filter_buy_range] = useState([0, buy_range_max]);
  const [filter_sell_range, set_filter_sell_range] = useState([
    0,
    sell_range_max
  ]);
  const [filter_trader, set_filter_trader] = useState('');
  const [sort, set_sort] = useState({ field: 'Name', asc: true });

  const filter = () => {
    let filtered = sort_list([...data], sort.field, sort.asc);
    if (filter_name.length > 2) {
      filtered = data.filter(
        r => r.Name.toLowerCase().indexOf(filter_name.toLowerCase()) > -1
      );
    }
    filtered = filtered.filter(
      r =>
        r.BuyPrice >= filter_buy_range[0] && r.BuyPrice <= filter_buy_range[1]
    );
    filtered = filtered.filter(
      r =>
        r.SellPrice >= filter_sell_range[0] &&
        r.SellPrice <= filter_sell_range[1]
    );
    if (filter_trader) {
      filtered = filtered.filter(
        r => r.Traders.toLowerCase().indexOf(filter_trader.toLowerCase()) > -1
      );
    }
    set_list_items(filtered);
  };

  const sort_list = (list, field, asc) => {
    return list.sort((a, b) => {
      if (!asc) {
        return a[field] < b[field] ? 1 : a[field] > b[field] ? -1 : 0;
      }
      return a[field] > b[field] ? 1 : a[field] < b[field] ? -1 : 0;
    });
  };

  const sort_items = () => {
    if (list_items.length) {
      set_list_items(sort_list([...list_items], sort.field, sort.asc));
    }
  };

  useEffect(() => {
    filter();
  }, [filter_name, filter_buy_range, filter_sell_range, filter_trader]);
  useEffect(() => {
    sort_items();
  }, [sort]);

  const handleSortClick = field => {
    set_sort({
      field,
      asc: sort.field === field ? !sort.asc : true
    });
  };

  const arrow = field =>
    sort.field === field ? (sort.asc ? 'arrow-up' : 'arrow-down') : null;

  return (
    <Container>
      <h1>Scum Trade Prices</h1>
      <div style={{ fontSize: '10px' }}>By Cuts-O-Meat</div>
      <HTMLTable>
        <thead>
          <tr>
            <th>
              <Button
                minimal
                text='Item Name'
                onClick={() => handleSortClick('Name')}
                rightIcon={arrow('Name')}
              />
            </th>
            <th>
              <Button
                minimal
                text='Buy Price'
                onClick={() => handleSortClick('BuyPrice')}
                rightIcon={arrow('BuyPrice')}
              />
            </th>
            <th>
              <Button
                minimal
                text='Sell Price'
                onClick={() => handleSortClick('SellPrice')}
                rightIcon={arrow('SellPrice')}
              />
            </th>
            <th>
              <Button
                minimal
                text='Trader'
                onClick={() => handleSortClick('Traders')}
                rightIcon={arrow('Traders')}
              />
            </th>
          </tr>
          <tr>
            <th>
              <InputGroup
                fill
                value={filter_name}
                onChange={e => set_filter_name(e.currentTarget.value)}
              />
            </th>
            <th>
              <Popover2
                content={
                  <div style={{ padding: 16 }}>
                    <Row style={{ marginBottom: 16 }}>
                      <Col>
                        <NumericInput
                          value={filter_buy_range[0]}
                          onValueChange={valueAsNumber =>
                            set_filter_buy_range([
                              valueAsNumber,
                              filter_buy_range[1]
                            ])
                          }
                        />
                      </Col>
                      <Col>
                        <NumericInput
                          value={filter_buy_range[1]}
                          onValueChange={valueAsNumber =>
                            set_filter_buy_range([
                              filter_buy_range[0],
                              valueAsNumber
                            ])
                          }
                        />
                      </Col>
                    </Row>
                    <RangeSlider
                      min={0}
                      max={buy_range_max}
                      stepSize={1}
                      labelStepSize={buy_range_max / 4}
                      labelRenderer={v => price(v)}
                      value={filter_buy_range}
                      onChange={r => set_filter_buy_range(r)}
                    />
                  </div>
                }
              >
                <Button
                  text={filter_buy_range.map(v => price(v)).join(' - ')}
                />
              </Popover2>
            </th>
            <th>
              <Popover2
                content={
                  <div style={{ padding: 16 }}>
                    <Row style={{ marginBottom: 16 }}>
                      <Col>
                        <NumericInput
                          value={filter_sell_range[0]}
                          onValueChange={valueAsNumber =>
                            set_filter_sell_range([
                              valueAsNumber,
                              filter_sell_range[1]
                            ])
                          }
                        />
                      </Col>
                      <Col>
                        <NumericInput
                          value={filter_sell_range[1]}
                          onValueChange={valueAsNumber =>
                            set_filter_sell_range([
                              filter_sell_range[0],
                              valueAsNumber
                            ])
                          }
                        />
                      </Col>
                    </Row>
                    <RangeSlider
                      min={0}
                      max={sell_range_max}
                      stepSize={1}
                      labelStepSize={sell_range_max / 4}
                      labelRenderer={v => price(v)}
                      value={filter_sell_range}
                      onChange={r => set_filter_sell_range(r)}
                    />
                  </div>
                }
              >
                <Button
                  text={filter_sell_range.map(v => price(v)).join(' - ')}
                />
              </Popover2>
            </th>
            <th>
              <HTMLSelect
                value={filter_trader}
                onChange={e => set_filter_trader(e.currentTarget.value)}
              >
                <option value=''>All Traders</option>
                <option value='Armorer'>Armorer</option>
                <option value='Bartender'>Bartender</option>
                <option value='Doctor'>Doctor</option>
                <option value='GeneralGoods'>General Goods</option>
                <option value='Mechanic'>Mechanic</option>
                <option value='Harbourmaster'>Harbour Master</option>
              </HTMLSelect>
            </th>
          </tr>
        </thead>
        <tbody>
          {list_items.map((item, i) => (
            <tr key={item.Name}>
              <td>{item.Name}</td>
              <td>{price(item.BuyPrice)}</td>
              <td>{price(item.SellPrice)}</td>
              <td>{item.Traders}</td>
            </tr>
          ))}
        </tbody>
      </HTMLTable>
    </Container>
  );
}

export default App;
